<script>

    import products_data from './assets/products.json';
    import categories_data from './assets/categories.json';

    import FoodInRest from './components/FoodInRest.vue';
    import CategoriesList from './components/CategoriesList.vue';
    import OrderSummary from './components/OrderSummary.vue';


    export default {
        components: {
            'categories-list': CategoriesList,
            'food-inrest': FoodInRest,
            'order-summary': OrderSummary
        },
        data() {
            return {
                categories: [],
                categories_products: {},
            }
        },
        beforeCreate() {
            this.$store.commit('init');
        },
        created() {
            let categoriesOrdered = categories_data.filter((categorie) => categorie.status).sort((a, b) => a.position - b.position);

            let categories = {};
            categoriesOrdered.forEach((categorie) => { categories[categorie.id] = categorie; });

            products_data.products.forEach((product) => {
                product.category_ids.map((categorie) => {
                    return categorie.id
                }).forEach((categorieId) => {
                    if (!categories[categorieId]['products']) {
                        categories[categorieId]['products'] = [];
                    }
                    categories[categorieId]['products'].push(product);
                })
            });

            this.categories = categoriesOrdered.filter((categorie) => categories[categorie.id]['products']);

            let categories_products = [];
            this.categories.forEach((categorie) => {categories_products.push(categories[categorie.id])});

            this.categories_products = categories_products;
        }
    }
</script>



